      mobile:null,
      windowSize: {x: 0, y: 0},
      dialog: false,
      token:null,
    }
  },

  mounted(){
    this.token = localStorage.getItem('token')
    // this.getdata()
  },

  computed: {

  },

  watch: {
    windowSize(){
      if (this.windowSize.x < 1000) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },
  },

  methods: {

    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
  },
}
</script>